* {
  font-family: "Nunito", sans-serif; }

body {
  margin: 0px;
  padding: 0px;
  color: #555555; }

a {
  text-decoration: none;
  color: #555555; }

#app {
  position: relative;
  padding: 60px 0px 60px 0px;
  min-height: 100vh;
  box-sizing: border-box;
  opacity: 0;
  animation: app-fade-in 0.5s forwards; }

@keyframes app-fade-in {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }
