#footer-wrapper {
  position: absolute;
  bottom: 0px;
  width: 100%;
  height: 60px; }
  #footer-wrapper #footer {
    max-width: 1000px;
    margin: auto;
    padding: 20px; }
    #footer-wrapper #footer #copy-right {
      display: inline-block;
      color: #ffffff;
      font-size: 12px; }
    #footer-wrapper #footer #signature {
      display: inline-block;
      font-size: 14px;
      line-height: 20px;
      color: #ffffff;
      text-align: center;
      float: right; }
      #footer-wrapper #footer #signature span {
        color: #f44336; }
      #footer-wrapper #footer #signature a {
        color: #a0a0a0; }
