.menu-button {
  position: relative;
  width: 20px;
  height: 20px;
  margin: 20px;
  float: left;
  transition: 0.3s;
  cursor: pointer; }
  .menu-button.expanded .menu-line:nth-child(1) {
    transform: rotate(-45deg); }
  .menu-button.expanded .menu-line:nth-child(2) {
    opacity: 0; }
  .menu-button.expanded .menu-line:nth-child(3) {
    transform: rotate(45deg); }
  .menu-button .menu-line {
    position: absolute;
    top: 50%;
    left: 0px;
    right: 0px;
    height: 2px;
    margin-top: -1px;
    background-color: #555555;
    transition: 0.3s;
    transform-origin: center; }
    .menu-button .menu-line:nth-child(1) {
      transform: translateY(-8px); }
    .menu-button .menu-line:nth-child(3) {
      transform: translateY(8px); }
