#header-wrapper {
  position: fixed;
  z-index: 100;
  top: -60px;
  width: 100%;
  height: 60px;
  box-sizing: border-box;
  transition: 0.3s;
  opacity: 0;
  animation: header-slide-in 0.5s forwards; }
  #header-wrapper.float {
    height: 60px;
    background-color: #ffffff;
    box-shadow: rgba(0, 0, 0, 0.5) 0px 1px 15px; }
    #header-wrapper.float * {
      color: #555555; }
    #header-wrapper.float #header #home-link span {
      color: #ff0000; }
    #header-wrapper.float #header #menu-button .menu-line {
      background-color: #555555; }
    #header-wrapper.float #back {
      bottom: 30px; }
  #header-wrapper * {
    color: #ffffff; }
  #header-wrapper #header {
    position: relative;
    max-width: 1000px;
    margin: auto;
    padding: 0px 20px; }
    #header-wrapper #header #home-link {
      display: inline-block;
      position: relative;
      height: 40px;
      margin: 10px 50px 10px 10px;
      vertical-align: top;
      z-index: 1;
      transition: 0.5s; }
      @media only screen and (max-width: 650px) {
        #header-wrapper #header #home-link {
          margin: 10px; } }
      #header-wrapper #header #home-link #logo {
        height: 40px;
        vertical-align: middle;
        transition: 0.5s; }
      #header-wrapper #header #home-link span {
        padding: 0px 20px;
        font-size: 18px;
        color: #ffffff;
        vertical-align: middle; }
    #header-wrapper #header #menu-button {
      display: none;
      width: 20px;
      height: 20px;
      float: right;
      z-index: 1; }
      @media only screen and (max-width: 650px) {
        #header-wrapper #header #menu-button {
          display: inline-block; } }
      #header-wrapper #header #menu-button .menu-line {
        background-color: #ffffff; }
      #header-wrapper #header #menu-button.expanded .menu-line {
        background-color: #555555; }
    #header-wrapper #header #menu {
      display: initial;
      float: right; }
      #header-wrapper #header #menu .link {
        display: inline-block;
        position: relative;
        padding: 20px;
        line-height: 20px;
        font-weight: 300;
        user-select: none;
        vertical-align: top; }
        #header-wrapper #header #menu .link::after {
          content: "";
          position: absolute;
          bottom: 0px;
          left: 50%;
          right: 50%;
          height: 3px;
          background-color: #ff0000;
          transition: 0.3s;
          z-index: -1; }
        #header-wrapper #header #menu .link.active::after {
          left: 0%;
          right: 0%; }
      @media only screen and (max-width: 650px) {
        #header-wrapper #header #menu {
          position: fixed;
          top: 0px;
          bottom: 0px;
          right: -100%;
          width: 100%;
          padding-top: 100px;
          background-color: rgba(255, 255, 255, 0.8);
          transition: 0.3s;
          overflow: hidden; }
          #header-wrapper #header #menu * {
            color: #555555; }
          #header-wrapper #header #menu .link {
            display: block;
            text-align: center;
            font-size: 24px;
            padding: 20px; }
            #header-wrapper #header #menu .link::after {
              height: 100%; }
            #header-wrapper #header #menu .link.active {
              color: #ffffff; }
          #header-wrapper #header #menu.show {
            right: 0px;
            overflow: auto; } }
      #header-wrapper #header #menu #menu-right {
        float: right; }
        @media only screen and (max-width: 650px) {
          #header-wrapper #header #menu #menu-right {
            font-size: 21px;
            text-align: center;
            margin-top: 40px;
            padding: 20px 0px;
            float: none; } }
        #header-wrapper #header #menu #menu-right #signup-link,
        #header-wrapper #header #menu #menu-right #login-link {
          display: inline-block;
          margin: 10px;
          padding: 10px;
          line-height: 20px;
          font-weight: 300;
          user-select: none; }
  #header-wrapper #back {
    display: inline-block;
    position: fixed;
    bottom: -40px;
    right: 30px;
    width: 20px;
    height: 20px;
    border-top: 6px solid #555555;
    border-left: 6px solid #555555;
    transform: rotate(45deg);
    transition: 0.3s; }
    #header-wrapper #back:after {
      content: "";
      display: inline-block;
      position: absolute;
      top: -5px;
      left: -5px;
      width: 20px;
      height: 20px;
      border-top: 4px solid #ffffff;
      border-left: 4px solid #ffffff; }

@keyframes header-slide-in {
  from {
    top: -60px;
    opacity: 0; }
  to {
    top: 0;
    opacity: 1; } }
