#background-wrapper {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background: #a0a0a0;
  background: radial-gradient(circle, #a0a0a0, #555555);
  z-index: -1; }
  #background-wrapper #background {
    position: absolute;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: left center;
    opacity: 0;
    animation: background-fade-in 0.5s forwards; }

@keyframes background-fade-in {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }
